.signin-outer {
  height: 100vh;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-size: cover;
  background-image: url("../../../assets/img/background.jpg");
}

.card-signin {
  border-radius: 40px;
  width: 100%;
  background-repeat: no-repeat;
  background-position: bottom left;
  background-size: 390px;
  box-shadow: 5px 6px 17px 0 rgba(0, 0, 0, 0.11);
  background-color: #ffffffbf;
}

.btn {
  border-radius: 7px;
  height: 38px;
  width: 100%;
  margin-top: 23px;
  background-color: #fd6400;
  box-shadow: none;
}

.card-body {
  margin: 0 auto;
  width: 80%;
}

.select-lang {
  float: right;
  width: 200px;
  margin-top: 16px;
  margin-right: 21px;
}

a {
  color: #3e3e3e;

  font-family: "GothamNarrowBold", sans-serif;
  font-size: 12px;
  font-weight: bold;
}

.signup a {
  color: #407ec9;
  font-size: 14px;
}

.w-75 {
  width: 480px !important;
}

.info {
  color: #3e3e3e;
  font-family: "GothamNarrowBold", sans-serif;
  font-size: 12px;
  font-weight: bold;
}

.lang {
  position: absolute;
  top: 12px;
  right: 16px;
}

.icon {
  position: relative;
  top: -7px;
  width: 11px;
}

.signin-outer .mat-form-field-wrapper {
  padding-bottom: 4px;
}

.errormsg {
  width: 307px;
  position: absolute;
  margin-top: -9%;
  line-height: 14px;
  font-size: 12px;
  height: 43px;
  display: flex;
  align-items: center;
  justify-content: center;
}

@media (max-width: 1025px) {
  .card-signin {
    background-size: 390px;
    box-shadow: none;
    background-color: transparent;
    border: none;
  }
}

@media only screen and (max-width: 550px) {
  .card-signin {
    border-radius: 40px;
    width: 100%;
    background-repeat: no-repeat;
    background-position: bottom left;
    background-size: 390px;
    box-shadow: none;
    background-color: transparent;
    border: none;
  }

  .w-75 {
    width: 100% !important;
  }

  .card-body {
    margin: 0 auto;
    width: 90%;
  }
}

.mat-icon-button ::ng-deep .mat-button-focus-overlay {
  display: none;
}

.remember-chk {
  color: #3e3e3e;
  font-family: "gothambook", sans-serif !important
}